export var Rank;
(function (Rank) {
    Rank[Rank["Ace"] = 1] = "Ace";
    Rank[Rank["Two"] = 2] = "Two";
    Rank[Rank["Three"] = 3] = "Three";
    Rank[Rank["Four"] = 4] = "Four";
    Rank[Rank["Five"] = 5] = "Five";
    Rank[Rank["Six"] = 6] = "Six";
    Rank[Rank["Seven"] = 7] = "Seven";
    Rank[Rank["Eight"] = 8] = "Eight";
    Rank[Rank["Nine"] = 9] = "Nine";
    Rank[Rank["Ten"] = 10] = "Ten";
    Rank[Rank["Jack"] = 11] = "Jack";
    Rank[Rank["Queen"] = 12] = "Queen";
    Rank[Rank["King"] = 13] = "King";
})(Rank || (Rank = {}));
export var Suit;
(function (Suit) {
    Suit[Suit["Clubs"] = 1] = "Clubs";
    Suit[Suit["Diamonds"] = 2] = "Diamonds";
    Suit[Suit["Hearts"] = 3] = "Hearts";
    Suit[Suit["Spades"] = 4] = "Spades";
    Suit[Suit["Joker"] = 5] = "Joker";
})(Suit || (Suit = {}));
export var State;
(function (State) {
    State[State["Default"] = 1] = "Default";
    State[State["Hidden"] = 2] = "Hidden";
    State[State["Dealing"] = 3] = "Dealing";
    State[State["Spinning"] = 4] = "Spinning";
    State[State["Flipped"] = 5] = "Flipped";
})(State || (State = {}));
export const Ranks = Object.keys(Rank).filter(x => !Number(x)).map(x => Rank[x]);
export const Suits = Object.keys(Suit).filter(x => !Number(x)).map(x => Suit[x]).filter(x => x != Suit.Joker);
export function getCardName(suit, rank) {
    if (suit == Suit.Joker)
        return Suit[suit];
    else
        return `${Rank[rank]} of ${Suit[suit]}`;
}
